import { Component, Host, Prop, h } from '@stencil/core';

@Component({
  tag: 'mt-grid-row',
  styleUrl: 'grid-row.scss',
  shadow: true,
})
export class GridRow {
  @Prop() cols: number = 2;

  render() {
    const { cols } = this;

    return (
      <Host class="mt-grid-row">
        <div class="mt-grid-row__col">
          <slot name="col-1"></slot>
        </div>

        {cols >= 2 && (
          <div class="mt-grid-row__col">
            <slot name="col-2"></slot>
          </div>
        )}

        {cols >= 3 && (
          <div class="mt-grid-row__col">
            <slot name="col-3"></slot>
          </div>
        )}
      </Host>
    );
  }
}
